@use 'setting' as *;

.loader {
  position: relative;
  width: 120px;
  height: 120px;
  margin: 0 auto;

  .flame-base,
  .flame-color {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
  }

  .flame-color {
    overflow: hidden;
    clip-path: inset(100% 0 0 0); // 
    transition: clip-path 0.2s ease-out;
  }

  .loader-percent {
    //position: absolute;
    //bottom: -100px;
    margin-top: 30px;
    transform: translateY(20px);
    width: 100%;
    text-align: center;
    font-weight: bold;

    p {
      font-family: $font-en;
      font-size: 27px;
      font-weight: 500;
      margin-top: 100px;
      font-weight: 900;

      span {
        font-family: $font-en;
        font-size: 27px;
        font-weight: 900;
      }
    }
  }
}

