.wave {
  transform: translateY(-100vh);
  z-index: 1000;

  &:before {
    width: 100%;
    height: 100vh;
    content: '';
    position: absolute;
    bottom: calc(-100vh + 10px);
    left: 0;
    background-color: #fefbf5;

    @include media(mobile) {
      bottom: calc(-100vh + 130px);
    }
  }
}

.page-loading {
