            }
          }
        }

        /**/
        .area {
          display: none;

          width: 100%;
          max-width: 1020px;
          margin: 0 auto;

          @include media(mobile) {
            width: 100%;
          }
        }

        .area.is-active {
          display: block;
          animation-name: displayAnime;
          animation-duration: 2s;
          animation-fill-mode: forwards;
        }

        .sec01-col-main {
          position: relative;
          z-index: 100;
          padding: 2px 0px;

          .swiper-wrapper {
            padding-top: 88px;

            .swiper-slide {
              width: 330px;

              @include media(mobile) {
                width: 225px;
              }

              a {
                display: block;
                width: 100%;
                position: relative;
                border: solid 1px #3c3636;
                border-radius: 30px;
                height: 100%;

                &:hover {
                  .fire {
                    animation: fire-reenter 0.8s ease-out forwards;
                  }
                }

                /*HOT*/
                .hot {
                  //display: block;
                  font-family: $font-en;
                  position: absolute;
                  color: #e81e23;
                  font-size: 25px;
                  font-weight: 900;
                  left: 10px;
                  top: -45px;

                  @include media(mobile) {
                    font-size: 18px;
                    top: -25px;
                  }
                }

                .fire {
                  width: 42px;
                  display: block;
                  position: absolute;
                  top: -70px;
                  left: 50%;
                  transform: translateX(-50%);
                  transition: opacity 0.3s, transform 0.3s;
                  opacity: 1;
                  @include media(mobile) {
                    width: 38px;
                    top: -65px;
                  }

                  img {
                    width: 100%;
                  }
                }

                .img {
                  text-align: center;
                  //border: solid 1px #3c3636;
                  border-radius: 29px 29px 0 0;
                  border-bottom: 1px solid #3c3636;
                  width: 100%;
                  overflow: hidden;
                  height: 200px;

                  @include media(mobile) {
                    height: 137px;
                  }

                  img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                  }
                }

                .img-info {
                  background-color: #f4ba1d;
                }

                .img-event {
                  background-color: #5cb232;
                }

                .img-food {
                  background-color: #e81e23;
                }

                .text {
                  width: 100%;
                  background-color: #fff;
                  border-radius: 0 0 30px 30px;
                  border-top: none;
                  display: flex;
                  flex-wrap: wrap;
                  justify-content: space-between;
                  align-items: center;
                  padding: 21px 20px 30px;

                  @include media(mobile) {
                    height: auto;
                  }

                  .text-info {
                    display: flex;
                    width: 100%;
                    flex-wrap: wrap;

                    @include media(middle) {
                      height: 48px;
                    }

                    .text-info-cat {
                      width: 50%;
                    }

                    .text-info-term {
                      width: 50%;
                      text-align: right;
                    }
                  }

                  .category {
                    display: inline-block;
                    padding: 2px 10px;
                    color: #3c3636;
                    font-size: 12px;
                    font-weight: 500;
                    letter-spacing: 0.1em;
                    border: solid 0.5px #3c3636;
                    border-radius: 25px;
                    white-space: nowrap;
                  }

                  .category-red {
                    background-color: #e81e23;
                    color: #fff !important;
                  }

                  .category-green {
                    background-color: #5cb232;
                    color: #fff !important;
                  }

                  .category-yellow {
                    background-color: #f4ba1d;
                  }

                  .category-blue {
                    background-color: #214995;
                    color: #fff !important;
                  }

                  .term {
                    font-size: 12.5px;
                    letter-spacing: -0.02em;
                    font-weight: 500;
                  }

                  h3 {
                    margin-top: 10px;
                    font-size: 16px;
                    width: 100%;
                    font-weight: 500;
                    letter-spacing: 0.1em;
                    height: 48px;

                    @include media(middle) {
                      font-size: 14px;
                      height: 42px;
                    }
                  }
                }
              }
            }
          }
        }

        @keyframes fire-reenter {
          0% {
            opacity: 1;
            transform: translate(-50%, 0);
          }

          30% {
            opacity: 0;
            transform: translate(-50%, -20px);
          }

          60% {
            opacity: 0;
            transform: translate(-50%, 40px); // 
          }

          100% {
            opacity: 1;
            transform: translate(-50%, 0);
          }
        }
      }
    }
  }

