        color: #3c3636;
        background-color: transparent;

        @include media(middle) {
          font-size: 16px;
        }
      }

      .price-table th:last-child,
      .price-table td:last-child {
        border-right: none;
      }

      .price-table tr:last-child td {
        border-bottom: none;
      }

      .price-table thead th {
        background: #3e3733;
        color: #fff;
        font-size: 19px;
        font-weight: bold;

        @include media(middle) {
          font-size: 16px;
        }
      }

      .price-table tbody td span.note {
        display: block;
        font-size: 12px;
        letter-spacing: 0.1em;
        font-weight: bold;
        color: #3c3636;
        margin-top: 4px;

        @include media(middle) {
          font-size: 10px;
        }
      }

      strong {
        font-size: 22px;
        letter-spacing: 0.1em;

        @include media(middle) {
          font-size: 20px;
        }
      }

      small {
        font-size: 18px;
        font-weight: bold;

        @include media(middle) {
          font-size: 13px;
        }
      }

      .col-left {
        width: 25%;
      }

      .col-center {
        width: 35%;
      }

      .col-right {
        width: 40%;
      }

      .sec05-wrap-txt {
        margin: 10px 0 25px;

        p {
          font-size: 14px;
          font-weight: 500;
          letter-spacing: 0.1em;
          line-height: 2;
          color: #3c3636;
          margin-bottom: 0;

          @include media(middle) {
            font-size: 11px;
          }
        }
      }

      .sec05-wrap-under-item {
        position: relative;

        &::before {
          display: block;
          position: absolute;
          content: '';
          width: 150px;
          height: 207px;
          background-image: url(("./assets/img/com/bg-item-09.png"));
          background-size: contain;
          background-repeat: no-repeat;
          left: -120px;
          bottom: -150px;

          @include media(middle) {
            width: 74px;
            height: 102px;
            left: 0;
            bottom: -180px;
          }
        }

        &::after {
          display: block;
          position: absolute;
          content: '';
          width: 336px;
          height: 459px;
          background-image: url(("./assets/img/com/bg-item-10_v2.png"));
          background-size: contain;
          background-repeat: no-repeat;
          right: -120px;
          bottom: -370px;
          z-index: -1;

          @include media(middle) {
            width: 259px;
            height: 312px;
            bottom: 0;
          }

          @include media(middle-small) {
            width: 159px;
            height: 212px;
            bottom: -210px;
            right: -15px;
          }
        }
      }

      .sec05-wrap-under {
        display: flex;
        border: solid 1px #707070;
        border-radius: 20px;
        overflow: hidden;

        li {
          font-size: 19px;
          font-weight: bold;
          letter-spacing: 0.1em;
          line-height: 1;
          padding: 26px 0;
          text-align: center;

          &:first-of-type {
            color: #fff;
            background-color: #3c3636;
            width: 277px;

            span {
              color: #fff;
            }
          }

          &:nth-of-type(2) {
            width: calc(100% - 277px);
            background-color: #fff;
          }

          span {
            font-size: 12px;
            font-weight: bold;
            letter-spacing: 0.1em;
          }
        }
      }

      .sec05-wrap-under-link {
        a {
          font-size: 18px;
          font-weight: bold;
          letter-spacing: 0.08em;
          color: #3c3636;
          border-bottom: solid 1px #3c3636;
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 198px;
          padding-bottom: 5px;
          margin: 55px auto 0;

          &:hover {
            i {
              transform: translateX(5px);
            }
          }

          @include media(mobile) {
            font-size: 15px;
            font-weight: bold;
            letter-spacing: 0.08em;
            color: #3c3636;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 279px;
            height: 51px;
            margin: 0 auto 0;
            background-color: #fff;
            border: solid 1px #3c3636;
            border-radius: 26px;
            position: relative;
            padding: 0;
          }

          i {
            width: 28px;
            height: 28px;
            background-image: url(("./assets/img/icon/icon-btn-black.png"));
            background-size: contain;
            background-repeat: no-repeat;
            display: inline-block;
            transition: transform 0.4s ease;

            @include media(mobile) {
              position: absolute;
              right: 20px;
              width: 22px;
              height: 22px;
              background-image: url(("./assets/img/icon/icon-btn-gray.png"));
            }
          }
        }
      }
    }
  }

